@use "global" as *;

.button {
  display: inline-block;
  width: rem(202); //
  text-align: left; //
  position: relative;
  z-index: 1;
  background-color: $green; //
  padding-top: rem(11); //
  padding-left: rem(40); //
  padding-bottom: rem(15); //
  border: rem(1) solid $green; //
  transition: background-color .3s;
}

.button--white {
  background-color: $white; //

}

.button.button--white {
  z-index: 100;
}
.button__text {
  font-size: rem(14); ///
  line-height: 1.5714; //
  color: $white; //
  font-family: $second-font-family;
  font-weight: $regular; //
  transition: color .3s; //

}

.button__text--green {
  color: $green; //
}

.button::before {
  //.button
  background: $white; //
  position: absolute; //
  top: 0; ////
  left: 0; ////
  content: ''; //
  width: 100%; //(X)
  height: 100%; //(X)
  transform: scale(0, 1); //(X)with,height,transform: scale(0, 1);
  transform-origin: left top; //
  transition: .2s cubic-bezier(0.45, 0, 0.55, 1); //transition cubic-bezier:
  z-index: -1;
}

.button--white::before {
  background-color: $white; //
}

.button::after {
  content: "";
  position: absolute; //
  top: rem(4); //
  left: rem(4); //
  bottom: rem(-4); //
  right: rem(-4); //
  border: rem(1) solid $green; //
  z-index: -2; //
}

.button--white::after {
  border: rem(1) solid $white; //
}

.button__text::after {
  //
  content: "";
  display: inline-block;
  width: rem(40); //
  height: rem(8); //
  border-bottom: rem(1) solid $white; //
  border-right: rem(1) solid $white; //
  position: absolute;
  top: 40%; //
  transform: skew(45deg); //
  margin-left: rem(5); //
  transition: border .3s;
}

.button__text--green::after {
  border-bottom: rem(1) solid $green; //
  border-right: rem(1) solid $green; //
}

.button:hover .button__text {
  //
  color: $green; //
}

.button:hover::before {
  transform: scale(1, 1); //buttonX
}

.button:hover .button__text::after {
  //
  border-color: $green; //
}



.button--position1 {
  display: none;

  @include mq("md") {
    display: inline;
    margin: 0 auto;
    margin-top: rem(16);
    display: flex;

  }
}



